body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: Roboto;
}

body {
  font-family: Roboto;
  font-size: 1.0em;
}

.App-header {
  display: flex;
  background-image: url(/static/media/me.841e5a62.png);
  background-position: center top;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.greeting {
  margin-bottom: 50px;
}

@media only screen and (max-width: 1000px) {
  .greeting {
    margin-bottom: 15%!important;
  }
}

.hello {
  font-size: 1.6em;
  margin-bottom: 10px;
}

.name {
  width: 100%; 
  text-align: center; 
  line-height: 0.1em;
  margin-bottom: 30px;
} 

.name span:before,
.name span:after {
  content: "";
  position: absolute;
  height: 5px;
  border-bottom: 2px solid white;
  top: 0;
  width: 20%;
}

.name span {
  display: inline-block;
  position: relative;  
  font-size: 2.0em;
  margin-top: 20px;
}

.name span:before {
  right: 100%;
  margin-right: 15px;
}
.name span:after {
  left: 100%;
  margin-left: 15px;
}

.name span { 
   padding:0 10px; 
}

.contact {
  align-self: flex-end;
  padding-right: 50px;
}

.contact > a > img {
  padding: 10px;
}

.navbar {
  display: flex;
  padding: 15px 10%;
  justify-content: space-between;
}

.navbar > a{
  cursor: pointer;
  text-decoration: none;
  color: black;
}

@media only screen and (max-width: 400px) {
 /*.App-header{
  max-height: 700px;
 }*/ 

  .name span:before,
  .name span:after {
    width: 40px;
  }

  .name span:before {
    right: 100%;
    margin-right: 5px;
  }
  .name span:after {
    left: 100%;
    margin-left: 5px;
  }

}

/*
@media only screen and (max-width: 1000px) {
  .App-header{
    min-height: 0!important;
    height: 600px!important;
  }
}
*/
@media only screen and (max-width: 600px) {

  .contact {
    align-self: center!important;
    padding-right: 0px!important;
  }

  .navbar {
    padding: 15px 15px!important;
  }

}

@media only screen and (max-width: 500px) {
 .navbar > a {
   font-size: 0.8em;
 } 
 .contact>a>img {
   height: 25px;
 }
}
.button {
    width: 100px;
    height: 20px;
    color: #1c76e5; 
    background-color: transparent;
    border: 1px #1c76e5 solid;
    border-radius: 5px; 
    text-decoration: none;
    line-height: 20px;
    padding: 10px;
}

.button:hover {
    background-color: rgba(28, 118, 229, 0.1);
}

.section {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 10% 30px 10%;
}

.section > * {
    margin: 20px 0px;
}

.lists {
    display: flex;
}

.sectionContent {
    display: flex;
    flex-wrap: wrap;
}

.subSection {
    margin: 0px 40px;
}

.section:nth-child(odd){
    background-color: #ebebec;
 
}

.section > p {
    max-width: 1000px;
}

.section > p > span {
    font-weight: 500;
}

.sectionTitle {
  text-align: center; 
  line-height: 0.1em;
  padding-bottom: 30px;
  padding-top: 0px;
  font-weight: 500;
  font-size: 1.6em;
} 

.sectionTitle span:before,
.sectionTitle span:after {
  content: "";
  position: absolute;
  height: 0px;
  border-bottom: 2px solid black;
  top: 0;
  width: 50px;
}

.sectionTitle span {
  display: inline-block;
  position: relative;  
}

.sectionTitle span:before {
    right: 100%;
    margin-right: 15px;
}
.sectionTitle span:after {
    left: 100%;
    margin-left: 15px;
}

.sectionTitle span { 
    padding:0 10px; 
}

h3 {
    color: #1c76e5;
    margin: 0;
}

@media only screen and (max-width: 600px) {

    .section {
        padding: 30px 5%;
    }
    .sectionTitle span {
        font-size: 0.7em;
    }
    .sectionTitle span { 
        padding:0 0px!important; 
     }

     .sectionTitle span:before,
     .sectionTitle span:after {
        width: 20px;
    }

     .sectionTitle span:before {
        margin-right: 10px!important;
      }
      .sectionTitle span:after {
        margin-left: 10px!important;
      }

      h3 {
          font-size: 1.0em;
      }
}

.accomplishments {
    flex-direction: column;
    justify-content: center;
}

.merged-top {
    padding-bottom: 0;
}

.merged-bottom {
    padding-top: 0;
    background-color: #ebebec;
 }

.accomplishment {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 15px 0px!important;
}

.accomplishment-info {
    flex-basis: 50%;
    flex-grow: 1;
    max-width: 500px;
    margin: 5px 0px;
}

@media only screen and (max-width: 600px) {
    .accomplishment-info {
        max-width: 80%;
    }
}

.accomplishment-title {
    font-size: 1.2em;
    font-weight: 500;
    color: #1c76e5;
}

.card {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    min-width: 250px;
    min-height: 250px;
    border-radius: 10px;
    box-shadow: #d9d9d9 5px 5px;
    margin: 5px 50px;
}

.card:hover {
    background-color: #d9d9d9;
}

